@import url('https://fonts.googleapis.com/css?family=Dosis|Poppins:100,400|Six+Caps');
@import url("https://use.typekit.net/ofs2dps.css");

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}
#demo, #github {
    position: relative;
}
#demo img, #github img {
    height: 100%;
    width: 100%;
    position: absolute;
}
.description-button {
    position: absolute;
    top: 0;
    left: 0;
        height: 100%;
        width: 100%;
    outline: none;
    border: none;
    color: #ffffff;
    font-family: "Poppins";
    background: none;
    font-size: 1.4rem;
}

#button-holder {
    width: 100vw;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 2rem;
    margin-bottom: 2rem;
}

#demo, #github {
    margin: 0 1rem;
    height: 3rem;
    width: 15rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#entry-master, #about-master, #contact-master {
    width: 100vw;
    position: absolute;
    top: 0;
    -webkit-transition: all 0.5s linear;
    -o-transition: all 0.5s linear;
    transition: all 0.5s linear;
}

#entry-master.hidden, #about-master.hidden, #contact-master.hidden {
    display: none;
    opacity: 0;
}


#entry-master.visible, #about-master.visible, #contact-master.visible {
    display: block;
    opacity: 1;
}

#about-master, #contact-master {
    background-color: #ffffff;
    height: 100vh;
}


/* header */

#entry-header {
    width: 100vw;
    height: 95vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    position: relative;
}
.pink {
    background-color: #ff1d84;
}
.blue {
    background-color: #00afd8;
}
.orange {
    background-color: #ff8827;
}
#entry-header-holder {
    height: 65vmin;
    width: 65vmin;
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

#entry-frame {
    width: 100%;
    height: 100%;
}
.header-tablet-landscape {
    position: absolute;
    height: 83.7%;
    width: auto;
    left: 18.6%;
    top: 8%;
}

@media(max-width: 610px) and (orientation: portrait) {
    #entry-header-holder {
        height: 90vmin;
        width: 90vmin;
    }
}
#entry-header-text {
    color: #ffffff;
    font-family: 'Poppins', sans-serif;
    font-weight: 100;
    font-size: 6vmin;
    text-transform: uppercase;
    position: absolute;
    bottom: 0%;
    letter-spacing: 0.1em;
    z-index: 1;
}

.divider-arrow {
    position: absolute;
    height: 6vh;
    width: 6vh;
    bottom: -3vh;
    -webkit-transform: rotateZ(45deg);
        -ms-transform: rotate(45deg);
            transform: rotateZ(45deg);
}

#description-arrow {
    background-color: #ffffff;
    z-index: 1;
}

#header-arrow {
    z-index: 1;
}

#entry-description {
    background-color: #ffffff;
    padding-top: 10vh;
    padding-bottom: 10vh;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;

}

#entry-gallery {
    min-height: 50vh;
    width: 100vw;
    position: relative;
}

h3, p {
    color: #    d32a87;
    font-family: 'Poppins';
}
h3 {

    font-weight: 100;
    font-size: 1.7em;
    margin-bottom: 0.2em;
    /* transform: rotateX(60deg) rotateY(0deg) rotateZ(-45deg); -for isometric headers */
}
p.description {

    /* padding-top: 3em; -for isometric headers */
    margin-top: 0;
    padding-left: 8%;
    padding-right: 8%;
}
.gallery-item-holder {
    padding: 10%;
    max-width: 80vw;
}
img.gallery {
    max-width: 100%;
    max-height: auto;
}
video.gallery {
    max-width: 100%;
    max-height: auto;
}
p.gallery-description {
    font-family: 'Poppins';
    color: #ffffff;
    padding-top: 1em;
}


/*
    THUMBNAILS
*/
#row-holder {
    width: 100vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}
.row {
    width: 80%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: row;
        flex-direction: row;
}
.thumbnail {
    height: 10vmin;
    width: 4vmin;
    margin: 2rem;
    position: relative;
    -webkit-transition: 0.5s all ease;
    -o-transition: 0.5s all ease;
    transition: 0.5s all ease;
}

.thumbnail.shape1 {
    height: 20vmin;
    width: 4vmin;
}

.thumbnail-content-holder {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
}
.thumbnail.no-match {
    -webkit-transform: scale(0.8, 0.8);
        -ms-transform: scale(0.8, 0.8);
            transform: scale(0.8, 0.8);
    opacity: 0.5;
}

button {
    position: absolute;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    outline: none;
    border: none;
    background: none;
    color: #ffffff;
}

button#entry-back, button#about-back, button#contact-back {
    position: absolute;
    top: 4%;
    left: 2%;
    height: 5vmin;
    width: 12vmin;
    background-position: center center;
    background-size: 100% 100%;
    text-transform: uppercase;
    font-family: 'Poppins';
    font-size: 1.5rem;
    margin: 0;
    padding: 0;
}

img.thumbnail-preview {
    position: absolute;
    height: 100%;
    max-width: 100%;
    pointer-events: none;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
}

/*
    NAV
*/

img#logo{
    top: 2vmin;
    left: 2vmin;
    height: 18vmin;
    width: auto;
    position: absolute;
}
#tags-list {
    width: 30vw;
    text-align: left;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
}
button.category, button.about, button.contact {
    position: relative;
    color: #000000;
}
button.category.selected {
    background-color: #666666;
    color: #ffffff;
}
button#about-back, button#contact-back {
    color: #000000;
}



